export const SET_LOCATIONS = 'SET_LOCATIONS'
export const SELECT_LOCATION = 'SELECT_LOCATION'
export const SELECT_WEEKDAY = 'SELECT_WEEKDAY'
export const SEARCH = 'SEARCH'

export const setLocations = (locations) => {
  return { action: SET_LOCATIONS, locations }
}

export const selectLocation = (location) => {
  return { action: SELECT_LOCATION, location }
}

export const selectWeekday = (weekday) => {
  return { action: SELECT_WEEKDAY, weekday }
}

export const search = (search) => {
  return { action: SEARCH, search }
}
