import { h, Component } from 'preact'
import { formatDistance } from '../utils'

const Distance = ({ distance }) => {
  return (
    <span class="distance">
      {`${formatDistance(distance)} away`}
    </span>
  )
}

const Location = ({ location, onSelect }) => {
  const onClick = (event) => {
    event.preventDefault()
    onSelect(location.id)
  }

  return (
    <li class="location">
      <a onClick={onClick}>
        {location.name}
        {location.distance ? <Distance distance={location.distance}/> : ''}
      </a>
    </li>
  )
}

export const Locations = ({ onSelectLocation, locations }) => {
  return (
    <ul class="locations-list">
      { locations.map((location) => <Location location={location} onSelect={onSelectLocation}/>)}
    </ul>
  )
}
